.l { *zoom: 1; }
.l:after { content: " "; display: table; clear: both; }
.l .l-col1 { float: left; width: 20%; }
.l .l-col2 { float: right; width: 70%; }

.menu { margin-top: 40pt; }
.menu ul li { border-bottom: 1px solid #d9d9d9; }
.menu ul li:first-child { border-top: 1px solid #d9d9d9; }
.menu ul li a { display: block; height: 50px; padding: 5px 25px 5px 15px; background: #FFF; text-align: center; font-size: 0; color: #444; font-size: 16px; line-height: 1; text-align: left; *zoom: 1; }
.menu ul li a:before { content: ""; display: inline-block; vertical-align: middle; width: 0; height: 100%; }
.menu ul li a:after { content: " "; display: table; clear: both; }
.menu ul li a i { display: inline-block; vertical-align: middle; margin-right: 15px; }
.menu ul li a span { float: right; display: block; margin-top: 10px; background-image: url("../img/icons.png"); width: 15px; height: 27px; background-position: -320px -184px; }

.faq { padding: 0; }

.slider .slide { position: relative; height: 100vh; padding-bottom: 140pt; background: #FFF; text-align: center; }
.slider .slide .slide__img { position: absolute; top: 0; left: 0; right: 0; bottom: 140pt; z-index: 1; }
.slider .slide .slide__img img { display: inline-block; vertical-align: bottom; height: 100%; }
.slider .slide .slide__text { position: absolute; top: auto; left: 0; right: 0; bottom: 0; height: 140pt; background: #FFF; padding-bottom: 30pt; z-index: 2; transform: translate3d(0, 0, 0); }
.slider .slide .slide__text p { display: block; padding: 12pt 24pt; color: #111; font-size: 16pt; line-height: 1.375; }
.slider .slide.slide-1 .slide__img { background: #47ADDC; }
@media only screen and (max-height: 568px) { .slider .slide.slide-1 .slide__img { top: 0; bottom: auto; } }
.slider .slide.slide-2 .slide__img { background: #A24EA3; }
@media only screen and (max-height: 568px) { .slider .slide.slide-2 .slide__img { top: auto; } }
.slider .slide.slide-3 .slide__img { background: #FFD013; }
@media only screen and (max-height: 568px) { .slider .slide.slide-3 .slide__img { top: auto; } }
.slider .slide.slide-4 .slide__img { background: #7CBC2C; }
@media only screen and (max-height: 568px) { .slider .slide.slide-4 .slide__img { top: 0; bottom: auto; } }

.mission { color: #111; margin: 30px 10px; }
.mission h1 { margin-bottom: 20px; font-size: 20px; font-weight: normal; }
.mission h2 { margin-bottom: 20px; font-size: 16px; font-weight: normal; }
.mission p { margin-bottom: 20px; font-size: 16px; }
.mission ol { list-style-type: decimal; }
.mission ul li { position: relative; margin-left: -25px; padding-left: 25px; }
.mission ul li:before { content: "—"; position: absolute; left: 0; top: 0; }
.mission ol, .mission ul { position: relative; margin: 0 0 20px 20px; }
.mission ol li, .mission ul li { margin-bottom: 10px; font-size: 16px; color: #7DBC3C; }
.mission ol li p, .mission ul li p { color: #111; }

.slick-slider { position: relative; display: block; box-sizing: border-box; -moz-box-sizing: border-box; -webkit-touch-callout: none; -webkit-user-select: none; -khtml-user-select: none; -moz-user-select: none; -ms-user-select: none; user-select: none; -ms-touch-action: pan-y; touch-action: pan-y; -webkit-tap-highlight-color: transparent; }

.slick-list { position: relative; overflow: hidden; display: block; margin: 0; padding: 0; }
.slick-list:focus { outline: none; }
.slick-loading .slick-list { background: white slick-image-url("ajax-loader.gif") center center no-repeat; }
.slick-list.dragging { cursor: pointer; cursor: hand; }

.slick-slider .slick-list, .slick-track, .slick-slide, .slick-slide img { -webkit-transform: translate3d(0, 0, 0); -moz-transform: translate3d(0, 0, 0); -ms-transform: translate3d(0, 0, 0); -o-transform: translate3d(0, 0, 0); transform: translate3d(0, 0, 0); }

.slick-track { position: relative; left: 0; top: 0; display: block; zoom: 1; }
.slick-track:before, .slick-track:after { content: ""; display: table; }
.slick-track:after { clear: both; }
.slick-loading .slick-track { visibility: hidden; }

.slick-slide { float: left; height: 100%; min-height: 1px; display: none; }
.slick-slide img { display: block; max-width: 100%; }
.slick-slide.slick-loading img { display: none; }
.slick-slide.dragging img { pointer-events: none; }
.slick-initialized .slick-slide { display: block; }
.slick-loading .slick-slide { visibility: hidden; }
.slick-vertical .slick-slide { display: block; height: auto; border: 1px solid transparent; }

.slick-prev, .slick-next { position: absolute; top: 50%; margin-top: -14px; font: 0/0 a; text-shadow: none; color: transparent; height: 50px; background: none; }
.slick-prev:focus, .slick-next:focus { outline: none; }
.slick-prev.slick-disabled, .slick-next.slick-disabled { opacity: 0.2; }
.slick-prev:before, .slick-next:before { height: 50px; line-height: 50px; }

.slick-prev { left: 0; }
.slick-prev:before { content: "←"; display: inline-block; vertical-align: middle; font-size: 61px; color: #fff; }

.slick-next { right: 0; }
.slick-next:before { content: "→"; display: inline-block; vertical-align: middle; font-size: 61px; color: #fff; }

.slick-dots { text-align: center; position: absolute; margin: 0; bottom: 20pt; left: 0; right: 0; }
.slick-dots li { position: relative; display: inline-block; padding: 0 7pt; }
.slick-dots li.slick-active button { width: 11pt; height: 11pt; background: #fff; border: 2pt solid #7cbc2c; }
.slick-dots li:before { display: none; }
.slick-dots button { display: block; width: 9pt; height: 9pt; background: #E8E8E8; cursor: pointer; font: 0/0 a; text-shadow: none; color: transparent; border-radius: 100%; }
.slick-dots button:hover { box-shadow: none; }

* { box-sizing: border-box; }

